.hero-wrapper {
  position: relative;
}

.logo {
  z-index: 30;
}

.hero-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(225deg, rgba(245, 245, 245, 0) 0%, rgba(255, 255, 255, 0.99) 100%);
  z-index: 1;
}

.hero-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(./assets/hero.png);
  flex-shrink: 0;
  background-size: cover;
  z-index: 0;
}

